import styled from '@emotion/styled';
import {Canvas} from '@react-three/fiber';
import {FC, useState} from 'react';
import './App.css';
import {BasicList} from './components/BasicList';
import {Splash} from './components/Splash';
import {GatsbyStars} from './three/GatsbyStars';

const StarsContainer = styled.div`
  width: 100%;
  height: 100%;
  background: #36135f;
`;

const ListContainer = styled.div`
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
`;

const App: FC = () => {
  const [loading, setLoading] = useState(true);
  if (loading) return <Splash onLoad={() => setLoading(false)} />;

  // return <BasicList />;

  return (
    <StarsContainer>
      <Canvas camera={{position: [0, 0, 1]}}>
        <GatsbyStars />
      </Canvas>
      <ListContainer>
        <BasicList />
      </ListContainer>
    </StarsContainer>
  );
};

export default App;
