import styled from '@emotion/styled';
import {FC, useState} from 'react';
import {
  getMessageImageUrlFromHash,
  Message,
  messages,
} from '../assets/messages';

const ModalContainer = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;

  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalBackground = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.7;

  cursor: pointer;
`;

const Modal = styled.div`
  position: relative;
  width: 700px;
  height: 600px;
  z-index: 2;
  overflow: hidden;
`;

const ModalBody = styled.div`
  background-color: white;
  border-radius: 20px;
  width: 100%;
  height: 100%;
  padding: 20px;

  display: flex;
  flex-direction: column;
`;

const CloseButton = styled.button`
  position: absolute;
  top: 10px;
  right: 10px;
`;

const MessageImageContainer = styled.div`
  height: 300px;
  margin-bottom: 20px;
  text-align: center;
`;
const MessageImageLink = styled.a`
  display: inline-block;
  height: 300px;
`;
const MessageImage = styled.img`
  max-width: 100%;
  max-height: 100%;
`;

const MessageBody = styled.div`
  flex: 1;
  overflow-y: auto;
`;

const MessageText = styled.div`
  text-align: justify;
  white-space: pre-line;
  padding: 0 5px;
`;

const MessageName = styled.p`
  text-align: right;
`;

export interface BasicModalProps {
  message: Message;
  onClose?: () => void;
  onNextClick?: () => void;
  onPreviousClick?: () => void;
}

export const BasicModal: FC<BasicModalProps> = ({
  message,
  onClose,
  onNextClick,
  onPreviousClick,
}) => {
  const imageUrl = getMessageImageUrlFromHash(message.hash);
  return (
    <ModalContainer>
      <ModalBackground onClick={() => onClose?.()} />
      <Modal>
        <ModalBody>
          <CloseButton onClick={onClose}>x</CloseButton>
          <MessageImageContainer>
            <MessageImageLink
              href={imageUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              <MessageImage src={imageUrl} alt="Star" />
            </MessageImageLink>
          </MessageImageContainer>
          <MessageBody>
            <MessageText>{message.message.trim()}</MessageText>
          </MessageBody>
          <MessageName> - {message.name}</MessageName>
          <div>
            <button onClick={onPreviousClick}>Previous</button>
            <button onClick={onNextClick}>Next</button>
          </div>
        </ModalBody>
      </Modal>
    </ModalContainer>
  );
};
